import React, { Component } from 'react';
import { Table, message, Button, Form, Input, Modal, DatePicker, Select, Radio, Icon } from 'antd';
import ip from '@model/api';
import common from '@utils/common';
import { getCookie } from '../helpers/cookies';

const { confirm } = Modal;

const { TextArea } = Input;

const dateFormat = 'YYYY-MM-DD';

const OPTIONS2 = [
    {
        value: '',
        label: ''
    },
    {
        value: '0',
        label: ''
    },
    {
        value: '1',
        label: ''
    }
];

class ArticleTab extends Component{
    constructor(props) {
        super(props);
        this.state = {
            addArticleInfo: {},
            articleInfo: {},
            articleDto: {
                title: null,
                publisherName: null,
                articleState: null,
                startDate: null,
                endDate: null,
                page: 1,
                size: 10,
            },
            total: 0,
            articleList: [],
            visible1: false,
            visible2: false,
            columns: [
                {
                    title: '',
                    width: 200,
                    dataIndex: 'title'
                },{
                    title: '',
                    width: 150,
                    dataIndex: 'articleState',
                    render: articleState => {
                        return articleState === 1 ? '' : ''
                    }
                },{
                    title: '',
                    width: 150,
                    render: articleInfo => {
                        return articleInfo.articleState === 1 ? common.unixToDate(articleInfo.publishedTime) : ''
                    }
                },{
                    title: '',
                    width: 100,
                    dataIndex: 'publisherName'
                },{
                    title: '',
                    width: 200,
                    dataIndex: 'introduction'
                },{
                    title: '',
                    width: 300,
                    dataIndex: 'imgSrc'
                },{
                    title: '',
                    width: 100,
                    dataIndex: 'isBanner',
                    render: isBanner => {
                        if(isBanner === 0){
                            return ''
                        }else if(isBanner === 1){
                            return ''
                        }
                    }
                },{
                    title: '',
                    width: 260,
                    render: articleInfo => {
                        return (
                            <div>
                                <Button type="primary" onClick={() => this.showDetails(articleInfo)}></Button>&nbsp;
                                <Button type="danger" onClick={() => this.delete(articleInfo)}></Button>&nbsp;
                                <Button onClick={() => this.edit(articleInfo)}></Button>
                            </div>
                        )
                    }
                }
            ],
            selectedItems1: [],
            selectedItems2: [],
            OPTIONS1: [],
            formLayout: 'horizontal',
            fileList: [],
            title: '',
            dialogTitle: ''
        }
    }

    componentDidMount() {
        this.getArticleList();
        this.getArticleCreaterList();
        this.getRecommend();
    }

    getArticleList() {
        let _this = this;
        ip.getArticleList(this.state.articleDto,res => {
            if(res.success){
                _this.setState({
                    articleList: res.data.list,
                    total: res.data.totalCount
                });
            }else{
                message.warning(res.msg);
            }
        })
    }

    getArticleCreaterList(){
        let _this = this;
        ip.getArticleCreaterList(res => {
            if(res.success){
                var data = [];
                res.data.forEach(item => {if(item){data.push(item)}});
                _this.setState({OPTIONS1: data});
            }else{
                message.warning(res.msg);
            }
        })
    }

    getRecommend(){
        let _this = this;
        ip.getRecommend(res => {
            if(res.success){
                _this.setState({title:res.data.title})
            }else{
                message.warning(res.msg);
            }
        })
    }

    // ,
    changePage(current){
        let data = Object.assign({}, this.state.articleDto, { page: current });
        this.setState({
            articleDto: data,
        },() => {
            this.getArticleList()
        })
    }

    // ,
    changePageSize(current, size){
        let data = Object.assign({}, this.state.articleDto, { size: size }, {page: 1});
        this.setState({
            articleDto: data,
        },() => {
            this.getArticleList()
        })
    }

    showModel = () => {
        this.setState({visible2: true,dialogTitle: '',addArticleInfo:{}});
    }

    operateTySubscribe = type => {
        let _this = this;
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(_this.state.dialogTitle === ''){
                    Object.assign(_this.state.addArticleInfo,
                        { publisherName:getCookie('alphaguUserName') },
                        { articleState:type },
                        { title:values.title },
                        { content:values.content },
                        { imgSrc:values.imgSrc },
                        { introduction:values.introduction },
                        { isBanner:values.isBanner })
                    ip.uploadArticle(_this.state.addArticleInfo,res => {
                        if(res.success){
                            message.success("");
                            _this.getArticleList();
                            _this.getRecommend();
                            _this.setState({visible2: false});
                            _this.props.form.resetFields();
                        }else{
                            message.warning(`${res.msg}`);
                        }
                    })
                }else if(_this.state.dialogTitle === ''){
                    Object.assign(_this.state.addArticleInfo,
                        { articleState:type },
                        { title:values.title },
                        { content:values.content },
                        { imgSrc:values.imgSrc },
                        { introduction:values.introduction },
                        { isBanner:values.isBanner })
                    ip.updateArticle(_this.state.addArticleInfo,res => {
                        if(res.success){
                            message.success("");
                            _this.getArticleList();
                            _this.getRecommend();
                            _this.setState({visible2: false});
                            _this.props.form.resetFields();
                        }else{
                            message.warning(`${res.msg}`);
                        }
                    })
                }
            }
        })
    }

    handleGetInputValue = event => {
        let value = event.target.value;
        let _this = this;
        value === '' ? Object.assign(_this.state.articleDto,{title:null}) : Object.assign(_this.state.articleDto,{title:value});
    }

    getStartDate = (e,date) => {
        let articleDto = Object.assign(this.state.articleDto,{startDate:date});
        this.setState({articleDto:articleDto})
        if(this.state.articleDto.endDate && date > this.state.articleDto.endDate){
            message.warning('');
        }
    }

    getEndDate = (e,date) => {
        let articleDto = Object.assign(this.state.articleDto,{endDate:date});
        this.setState({articleDto:articleDto})
        if(this.state.articleDto.startDate && date < this.state.articleDto.startDate){
            message.warning('');
        }
    }

    search = () => {
        let _this = this;
        let articleDto = Object.assign(_this.state.articleDto,{page:1});
        this.setState({
            articleDto:articleDto
        });
        this.getArticleList();
    }

    handleCancel = e => {
        this.setState({ visible2: false });
        this.props.form.resetFields();
    }

    delete(articleInfo){
        let _this = this;
        confirm({
            title: '?',
            content: '',
            onOk() {
                ip.deleteArticle('?id='+articleInfo.id,res => {
                    if(res.data){
                        message.success('');
                        _this.getArticleList();
                    }else{
                        message.warning('');
                    }
                })
            },
            onCancel() {},
        });
    }

    edit(articleInfo){
        this.setState({visible2: true,dialogTitle:'',addArticleInfo:articleInfo});
    }

    showDetails(articleInfo){
        this.setState({articleInfo:articleInfo,visible1: true});
    }

    handleChange1 = selectedItems => {
        let articleDto = Object.assign(this.state.articleDto,{publisherName:selectedItems});
        this.setState({
            articleDto: articleDto
        })
    }

    handleChange2 = selectedItems => {
        let articleDto = Object.assign(this.state.articleDto,{articleState:selectedItems});
        this.setState({
            articleDto: articleDto
        })
    }

    upload() {
        const input = document.getElementById('input')
        input.click()
    }

    change(e) {
        var _this = this;
        //() 2 M = 2097152 B 2M
        var AllowImgFileSize = 2100000;
        //
        var file = e.target.files[0];
        var files = file.name.split('.')
        var name = files[files.length - 1]
        var type = ['gif', 'png', 'jpg', 'svg', 'jpeg']
        // 
        if (type.indexOf(name) === -1) {
            message.info(`.${name}`)
            return
        }
        // 
        if (AllowImgFileSize < file.result) {
            message.info('2M');
            return
        }
        //Data URL
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = function () {
            let dataURL = reader.result;
            let image_base64=dataURL.split(",")[1];
            image_base64 = image_base64.replace("data:image/png;base64,","");
            let pictureindex = dataURL.indexOf(',');
            let picture = dataURL.substring(pictureindex+1);
            ip.uploadPic('name='+file.name+'&picture='+picture,res => {
                if(res.success){
                    let addArticleInfo = Object.assign(_this.state.addArticleInfo,{imgSrc:res.data});
                    _this.setState({addArticleInfo:addArticleInfo})
                }else{
                    message.warning(res.msg);
                }
            })
        }
    }

    render() {
        const { formLayout } = this.state;
        const formItemLayout =
            formLayout === 'horizontal'
                ? {
                    labelCol: { span: 4 },
                    wrapperCol: { span: 14 },
                }
                : null;
        const { getFieldDecorator } = this.props.form;
        const { selectedItems1 } = this.state;
        const { selectedItems2 } = this.state;
        const filteredOptions1 = this.state.OPTIONS1.filter(o => !selectedItems1.includes(o));
        const filteredOptions2 = OPTIONS2.filter(o => !selectedItems2.includes(o));
        const { addArticleInfo } = this.state;

        var inputStyle = { display:'none' };

        return (
            <div>
                <div className="table-search">
                    <Form>
                        <Form.Item label="">
                            <Input onChange={this.handleGetInputValue} placeholder="" />
                        </Form.Item>
                        <Form.Item label="">
                            <DatePicker onChange={this.getStartDate} format={dateFormat}/>
                        </Form.Item>
                        <Form.Item label="">
                            <DatePicker onChange={this.getEndDate} format={dateFormat}/>
                        </Form.Item>
                        <Form.Item label="">
                            <Select placeholder="" style={{ width: 200,marginRight: 10 }} onChange={this.handleChange1}>
                                <Select.Option key="" value=""></Select.Option>
                            {filteredOptions1.map(item => (
                                <Select.Option key={item} value={item}>
                                    {item}
                                </Select.Option>
                            ))}
                            </Select>
                        </Form.Item>
                        <Form.Item label=" ">
                            <Select placeholder="" style={{ width: 200,marginRight: 10 }} onChange={this.handleChange2}>
                            {filteredOptions2.map(item => (
                                <Select.Option key={item.value} value={item.value}>
                                    {item.label}
                                </Select.Option>
                            ))}
                            </Select>
                        </Form.Item>
                        <Form.Item label=" " className="table-search-button">
                            <Button type="primary" onClick={this.search}></Button>&nbsp;
                            <Button type="primary" onClick={this.showModel} icon="plus"></Button>
                        </Form.Item>
                    </Form>
                </div>
                <Table
                    rowKey="id"
                    scroll={{x:800}}
                    columns={this.state.columns}
                    dataSource={this.state.articleList}
                    bordered
                    title={() => {return (
                        <span className="table-title">
                            <span>{this.state.title}</span>
                        </span>
                    )}}
                    pagination={{
                        showSizeChanger: true,
                        showQuickJumper: true,
                        showTotal: () => `${this.state.total}`,
                        pageSize: this.state.articleDto.size,
                        current: this.state.articleDto.page,
                        total: this.state.total,
                        onShowSizeChange: (current, size) => this.changePageSize(current, size),
                        onChange: (current) => this.changePage(current)
                    }}
                />
                {/*  */}
                <Modal
                    className="model-dialog"
                    visible={this.state.visible1}
                    title=""
                    onCancel={() => {this.setState({visible1:false})}}
                    footer={[<Button key="submit" type="primary" onClick={() => {this.setState({visible1:false})}}></Button>]}
                >
                    <p>{this.state.articleInfo.title}</p>
                    <p><img className="model-dialog-img" src={this.state.articleInfo.imgSrc}/></p>
                    <p>{this.state.articleInfo.publisherName}</p>
                    <p>{this.state.articleInfo.content}</p>
                </Modal>
                {/*  */}
                <Modal
                    layout={formLayout}
                    className="model-dialog"
                    title={this.state.dialogTitle}
                    visible={this.state.visible2}
                    onCancel={this.handleCancel}
                    footer={null}
                >
                    <Form className="model-form">
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('title',  {
                                rules: [{ required: true, message: '45',}],
                                initialValue: addArticleInfo.title
                                })(<Input placeholder="" />)}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('introduction',  {
                                rules: [{ required: true, message: ''}],
                                initialValue: addArticleInfo.introduction
                                })(<Input placeholder="" />)}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('imgSrc', {
                                rules: [{ required: true, message: ''}],
                                initialValue: addArticleInfo.imgSrc})(<Input disabled />)}
                            <input onChange={this.change.bind(this)} accept="image/gif,image/jpeg,image/jpg,image/png,image/svg" style={inputStyle} id="input" type="file" />
                            <Button onClick={this.upload.bind(this)}>
                                <Icon type="upload" />
                            </Button>
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('isBanner', {
                                rules: [{ required: true, message: ''}],
                                initialValue: addArticleInfo.isBanner
                                })(<Radio.Group>
                                    <Radio value={1}></Radio>
                                    <Radio value={0}></Radio>
                                  </Radio.Group>)}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('content',  {
                                rules: [{ required: true, message: ''}],
                                initialValue: addArticleInfo.content
                                })(<TextArea autoSize={{ minRows: 4 }} placeholder="" />)}
                        </Form.Item>
                        <Form.Item label="" className="model-form_button">
                            <Button type="primary" onClick={() => {this.operateTySubscribe(0)}}></Button>
                            <Button type="primary" onClick={() => {this.operateTySubscribe(1)}}></Button>
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        )
    }
}

export default Form.create()(ArticleTab)
