import React, { Component } from 'react';
import { Table, Button, Modal, Form, Input, message, Radio } from 'antd';
import ip from '@model/api';
import common from '@utils/common';
import { getCookie } from '../helpers/cookies';

const { confirm } = Modal;

class Usermanage extends Component{
    constructor(props) {
        super(props);
        this.state = {
            data: {
                current: 1,
                userList: [],
                pageSize: 10,
                total: 0
            },
            userDetail: {},
            tyUserDetail: {},
            visible: false,
            visible1: false,
            formLayout: 'horizontal',
            dialogTitle: '',
            isEdit: false,
            columns: [
                {
                    title: '',
                    dataIndex: 'userName'
                },{
                    title: '',
                    dataIndex: 'createTime',
                    render: (createTime) => {
                        return createTime ? createTime.substring(0,10) : null
                    }
                },{
                    title: '',
                    dataIndex: 'userType',
                    render: (userType) => {
                        if(getCookie('alphaguUserType') === '3'){
                            return (<span>{userType}</span>)
                        }else{
                            if(userType === '0'){
                                return (<span></span>)
                            }else if(userType === '1'){
                                return (<span></span>)
                            }else if(userType === '2'){
                                return (<span></span>)
                            }
                        }
                    }
                },{
                    title: '',
                    dataIndex: 'email'
                },{
                    title: '',
                    dataIndex: 'phone'
                },{
                    title: '',
                    width: 260,
                    render: (info) => {
                        if(getCookie('alphaguUserType') === '3'){
                            return <Button onClick={() => { return this.showDetail(info) }}></Button>
                        }else{
                            if(info.userName === 'admin'){
                                return (
                                    <span>
                                        <Button onClick={() => { return this.resetPwd(info) }}></Button>
                                    </span>
                                )
                            }else{
                                return (
                                    <span>
                                        <Button type="primary" onClick={() => { return this.getUserDetailById(info) }}></Button>&nbsp;
                                        <Button type="danger" onClick={() => { return this.deleteUser(info) }}></Button>&nbsp;
                                        <Button onClick={() => { return this.resetPwd(info) }}></Button>
                                    </span>
                                )
                            }
                        }
                    }
                }
            ]
        }
    }

    componentDidMount() {
        this.getDate();
    }

    getDate(){
        let _this = this;
        let param = [
            {
                name: 'page',
                value: this.state.data.current
            },
            {
                name: 'size',
                value: this.state.data.pageSize
            },
            {
                name: 'type',
                value: getCookie('alphaguUserType')
            }
        ];
        let con = common.joinSearchParam(param);
        ip.getUserList(con,res => {
            if(res.success){
                let data = Object.assign({}, this.state.data, { total: res.data.totalCount }, { userList: res.data.userList });
                _this.setState({data:data})
            }else{
                message.warning(res.msg);
            }
        })
    }

    // ,
    changePage(current){
        let data = Object.assign({}, this.state.data, { current: current });
        this.setState({
            data: data,
        },() => {
            this.getDate()
        })
    }

    // ,
    changePageSize(current, size){
        let data = Object.assign({}, this.state.data, { pageSize: size }, {current: 1});
        this.setState({
            data: data,
        },() => {
            this.getDate()
        })
    }

    showModal = () => { this.setState({ userDetail: {}, visible: true, dialogTitle: '', isEdit: false }) }

    handleCancel = e => { this.setState({ visible: false }) };

    editUser = () => {
        let _this = this;
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(_this.state.dialogTitle === ''){
                    ip.userAdd(values, res => {
                        if(res.success){
                            message.success(res.msg);
                            _this.setState({visible: false});
                            _this.getDate();
                            _this.props.form.resetFields();
                        }else{
                            message.warning(res.msg);
                        }
                    })
                }else if(_this.state.dialogTitle === ''){
                    Object.assign(_this.state.userDetail, {userType: values.userType, email: values.email, phone: values.phone})
                    ip.updateUserInformation(_this.state.userDetail, res => {
                        if(res.success){
                            message.success(res.data);
                            _this.setState({visible: false});
                            _this.getDate();
                            _this.props.form.resetFields();
                        }else{
                            message.warning(res.msg);
                        }
                    })
                }
            }
        });
    }

    deleteUser(info){
        let _this = this;
        confirm({
            title: '',
            content: '?',
            onOk() {
                ip.deleteUser(info.id, res => {
                    if(res.success){
                        message.success(res.data);
                        Object.assign(_this.state.data, { current: 1 });
                        _this.getDate();
                    }else{
                        message.warning(res.msg);
                    }
                })
            },
            onCancel() {}
        })
    }

    resetPwd(info){
        confirm({
            title: '',
            content: '?',
            onOk() {
                ip.resetPwd(info.id, res => {
                    if(res.success){
                        message.success(res.data);
                    }else{
                        message.warning(res.msg);
                    }
                })
            },
            onCancel() {}
        })
    }

    showDetail(info){
        this.setState({tyUserDetail: info, visible1: true});
    }

    getUserDetailById(info){
        this.setState({userDetail: info, visible: true, dialogTitle: '', isEdit: true});
    }

    render() {
        const { formLayout } = this.state;
        const formItemLayout =
            formLayout === 'horizontal'
                ?{
                    labelCol: { span: 4 },
                    wrapperCol: { span: 14 },
                }
                : null;
        const { getFieldDecorator } = this.props.form;
        const { userDetail } = this.state;
        return (
            <div>
                <Table
                    rowKey="id"
                    scroll={{x:800}}
                    columns={this.state.columns}
                    dataSource={this.state.data.userList}
                    bordered
                    title={() => {return (
                        <span className="table-title">
                            <span></span>
                            <Button type="primary" onClick={this.showModal}></Button>
                        </span>
                    )}}
                    pagination={{
                        showSizeChanger: true,
                        showQuickJumper: true,
                        showTotal: () => `${this.state.data.total}`,
                        pageSize: this.state.data.pageSize,
                        current: this.state.data.current,
                        total: this.state.data.total,
                        onShowSizeChange: (current, size) => this.changePageSize(current, size),
                        onChange: (current) => this.changePage(current)
                    }}
                />
                {/*  */}
                <Modal
                    className="model-dialog"
                    visible={this.state.visible1}
                    title=""
                    onCancel={() => {this.setState({visible1:false})}}
                    footer={[<Button key="submit" type="primary" onClick={() => {this.setState({visible1:false})}}></Button>]}
                >
                    <p>{this.state.tyUserDetail.userName}</p>
                    <p>{this.state.tyUserDetail.createTime}</p>
                    <p>{this.state.tyUserDetail.userType}</p>
                    <p>{this.state.tyUserDetail.email}</p>
                    <p>{this.state.tyUserDetail.phone}</p>
                </Modal>
                {/* / */}
                <Modal
                    layout={formLayout}
                    className="model-dialog"
                    title={this.state.dialogTitle}
                    visible={this.state.visible}
                    onCancel={this.handleCancel}
                    footer={null}
                >
                    <Form className="model-form">
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('userName',  {
                                rules: [{ required: true, message: '',}],
                                initialValue: userDetail.userName
                                })(<Input disabled={this.state.isEdit} placeholder="" />)}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('userType', {
                                rules: [{ required: true, message: ''}],
                                initialValue: userDetail.userType
                                })(<Radio.Group>
                                    <Radio value={'0'}></Radio>
                                    <Radio value={'1'}></Radio>
                                    <Radio value={'2'}></Radio>
                                  </Radio.Group>)}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('email',  {
                                rules: [
                                    {
                                        type: 'email',
                                        message: '',
                                    },
                                    {
                                        required: true,
                                        message: ''
                                    }
                                ],
                                initialValue: userDetail.email
                                })(<Input placeholder="" />)}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('phone',  {
                                rules: [
                                    {
                                        required: false,
                                        message: ''
                                    },
                                    {
                                        pattern: new RegExp(/^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/, "g"),
                                        message: ''
                                    }
                                ],
                                initialValue: userDetail.phone
                                })(<Input placeholder="" />)}
                        </Form.Item>
                        <Form.Item label="" className="model-form_button">
                            <Button key="cancel" onClick={this.handleCancel}></Button>,
                            <Button key="ok" type="primary" onClick={this.editUser}></Button>
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        )
    }
}

export default Form.create()(Usermanage)
