import React, { Component } from 'react';
import { Table, message, Button, Modal } from 'antd';
import ip from '@model/api';

const { confirm } = Modal;

class OrderExcept extends Component{
    constructor(props) {
        super(props);
        this.state = {
            data: {},
            columns: [
                {
                    title: '',
                    dataIndex: 'code'
                },{
                    title: '',
                    dataIndex: 'paymentType',
                    render: (paymentType) => {
                        if(paymentType === 0){
                            return (<span></span>)
                        }else if(paymentType === 1){
                            return (<span></span>)
                        }
                    }
                },{
                    title: '',
                    dataIndex: 'state',
                    render: (state) => {
                        if(state === 0){
                            return (<span></span>)
                        }else if(state === 1){
                            return (<span></span>)
                        }else if(state === 2){
                            return (<span></span>)
                        }
                    }
                },{
                    title: '',
                    dataIndex: 'oprator'
                },{
                    title: '',
                    dataIndex: 'subscribeName'
                },{
                    title: '',
                    dataIndex: 'tradeDescription'
                },{
                    title: '',
                    dataIndex: 'orderStatus'
                },{
                    title: '',
                    render: (info) => {
                        return <Button type="primary" onClick={() => this.updateStatus(info)}></Button>
                    }
                }
            ],
            orderExceptList: []
        }
    }

    componentDidMount(){
        this.getExceptOrderList();
    }

    getExceptOrderList(){
        let _this = this;
        ip.exceptOrderList(res => {
            if(res.success){
                _this.setState({orderExceptList: res.data.list})
            }else{
                message.warning(res.msg);
            }
        })
    }

    updateStatus(info){
        confirm({
            title: '',
            content: '?',
            onOk() {
                ip.setOrderNormal('?code='+info.code, res => {
                    if(res.success){
                        message.success(res.data);
                    }else{
                        message.warning(res.msg);
                    }
                })
            },
            onCancel() {},
          });
    }

    render() {
        return (
            <div>
                <Table
                rowKey="id"
                columns={this.state.columns}
                dataSource={this.state.orderExceptList}
                bordered
                title={() => {return (
                    <span className="table-title">
                        <span></span>
                    </span>
                )}} />
            </div>
        )
    }

}

export default OrderExcept