import React, { Component } from 'react';
import { Table, Button, message, Form, Select } from 'antd';
import ip from '@model/api';
import common from '@utils/common';

const OPTIONS = [
    {
        value: null,
        label: ''
    },
    {
        value: 1,
        label: ''
    },
    {
        value: 2,
        label: ''
    },
    {
        value: 3,
        label: ''
    }
];

class TYuserfeefback extends Component{
    constructor(props) {
        super(props);
        this.state = {
            data: {
                page: 1,
                size: 10,
                status: null,
                type: 2 //TY
            },
            total: 0,
            APPfeedbackList: [],
            columns: [
                {
                    title: '',
                    dataIndex: 'userName'
                },{
                    title: '',
                    dataIndex: 'title'
                },{
                    title: '',
                    dataIndex: 'type',
                    render: type => {
                        if(type === 1){
                            return (<span></span>)
                        }else if(type === 2){
                            return (<span></span>)
                        }else if(type === 3){
                            return (<span></span>)
                        }
                    }
                },{
                    title: '',
                    dataIndex: 'address'
                },{
                    title: '',
                    dataIndex: 'email'
                },{
                    title: '',
                    dataIndex: 'opratorName'
                }
            ],
            selectedItems: [],
            inputValue: null
        }
    }

    componentDidMount() {
        this.getAllFeedbackList();
    }

    getAllFeedbackList() {
        let _this = this;
        let param = [
            {
                name: 'page',
                value: this.state.data.page
            },
            {
                name: 'size',
                value: this.state.data.size
            },
            {
                name: 'status',
                value: this.state.data.status
            },
            {
                name: 'type',
                value: this.state.data.type
            }
        ];
        let con = common.joinSearchParam(param);
        ip.getAllFeedbackList(con,res => {
            if(res.success){
                _this.setState({
                    APPfeedbackList: res.data.feedbackList,
                    total: res.data.totalCount
                });
            }else{
                message.warning(res.msg);
            }
        })
    }

    // ,
    changePage(current){
        let data = Object.assign({}, this.state.data, { page: current });
        this.setState({
            data: data,
        },() => {
            this.getAllFeedbackList()
        })
    }

    // ,
    changePageSize(current, size){
        let data = Object.assign({}, this.state.data, { size: size }, {page: 1});
        this.setState({
            data: data,
        },() => {
            this.getAllFeedbackList()
        })
    }

    handleChange = selectedItems => {
        let data = Object.assign(this.state.data,{status:selectedItems});
        this.setState({
            data: data
        })
      }

    search = () => {
        let data = Object.assign(this.state.data,{page:1});
        this.setState({
            data: data
        })
        this.getAllFeedbackList()
    }

    render() {
        const { vertical } = this.state;
        const { selectedItems } = this.state;
        const filteredOptions = OPTIONS.filter(o => !selectedItems.includes(o));

        return (
            <div>
                <div className="table-search">
                    <Form layout={vertical}>
                        <Form.Item label="">
                            <Select placeholder="" style={{ width: 200,marginRight: 10 }} onChange={this.handleChange}>
                            {filteredOptions.map(item => (
                                <Select.Option key={item.value} value={item.value}>
                                    {item.label}
                                </Select.Option>
                            ))}
                            </Select>
                        </Form.Item>
                        <Form.Item label=" " className="table-search-button">
                            <Button type="primary" onClick={this.search}></Button>
                        </Form.Item>
                    </Form>
                </div>
                <Table
                    rowKey="id"
                    columns={this.state.columns}
                    dataSource={this.state.APPfeedbackList}
                    bordered
                    title={() => {return (
                        <span className="table-title">
                            <span></span>
                        </span>
                    )}}
                    pagination={{
                        showSizeChanger: true,
                        showQuickJumper: true,
                        showTotal: () => `${this.state.total}`,
                        pageSize: this.state.data.size,
                        current: this.state.data.page,
                        total: this.state.total,
                        onShowSizeChange: (current, size) => this.changePageSize(current, size),
                        onChange: (current) => this.changePage(current)
                    }}
                />
            </div>
        )
    }
}

export default TYuserfeefback
